.container{
    width: 70vw;
    border-radius: 20px;
    box-shadow: 5px 5px 10px 5px #eee;
    min-height: 10vh;
}
.smcontainer{
    width: fit-content;
    padding-left: 1%;
    padding-right: 1%;
    border-radius: 25px;
    box-shadow: 5px 5px 10px 7px #eee;
    display: inline-block;
    margin: 5%;
}
.typo{
    font-weight: 300;
}
.bu{
    background: #fff;
    color: #444;
    font-weight: 200;
    font-size: 140%;
    box-shadow: none;
    margin-left:1%;
    border:none;
}
.bu:hover{
    background: #ddd;
    color:#000;
    border-radius: 25px;
}